      <div>
        <v-col cols="12">
          <mrc_ess_datePicker @getTime='paramsChange'
                              :disabled="disabled"
                              :label="$t('label_Handover_time')"
                              v-model="list.handover_date"></mrc_ess_datePicker>
        </v-col>
        <v-col cols="12">
          <!-- <v-text-field label="交接内容"
                      v-model="list.handover_detail"
                      :readonly="disabled">
        </v-text-field> -->
          <mrc_ess_multiText :label="$t('label_Handover_content')"
                             :isNational="judegNational('handover_detail')"
                             v-model="list.handover_detail"
                             :readonly="disabled">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <mrc_ess_brandupload :isShow='false'></mrc_ess_brandupload>
        </v-col>
      </div>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
  </v-card>
