<script> 
import {
  getHandoverList
  , addHandover
  , updateHandover
  , deleteHandover
  , detailHandover
} from '@/service/api/staffmsg.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      list: {},
      btnIsLoading: false,
      viewList: {}
    };
  },
  computed: {
    company_uuid () { //当前选中公司uuid
      return this.$store.state.currentCompany
    },
    turnover_person () {
      return { prompt_name: 'turnover_person' }
    }
  },
  created () { },
  watch: {},
  async mounted () {
    await this.national('mrbase_sys_change_dimission')
    if (this.$store.state.uuid) {
      this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    paramsChange () { },
    changeList (val) {
      this.viewList = val
    },
    async getDetail () {
      const res = await detailHandover({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      if (this.$store.state.uuid) {
        String(this.list.handover_date).indexOf('-') == -1 ? this.list.handover_date = this.$moment(this.list.handover_date) : ''
        updateHandover(this.$deletefield(null, this.list)).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        addHandover(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'staffConnect' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'staffConnect'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      apply_staff_uuid: {
        required
      },
      handover_staff_uuid: {
        required
      }
    }
  },
};
</script>

